import React, { Component  } from 'react'
import { StaticQuery, graphql } from "gatsby"
import WhatsappImg from "../images/whatsapp-logo.png"


export default function Whatsapp () {
  
  return (
    <StaticQuery
      query = {graphql`
      query Whatsapp {
          allWpPage(filter: {slug: {eq: "whatsapp"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render = { data => (
        
       <div>
        <img class="w-50" src={WhatsappImg} alt='whatsapp' />
        Whatsapp
        </div>

      )}
    />
  )
}